.container{
    display:flex;
    flex-direction: column;
    align-items: center;
    
    margin:50px 0;
}

.card{
    margin: 0 2% !important;
}

.infected{
    border-bottom: 10px solid blue ;
}

.recovered{
    border-bottom: 10px solid green;
}

.deaths{
    border-bottom: 10px solid red;
}

@media(max-width:770px){
    .card{
        margin:2% 0 !important;
    }
}